@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&family=Roboto&display=swap');


* {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #fff;
  color: #222;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
}
a {
  text-decoration-line: none;
  color: #797977;
}

.conteiner {
  width: 1280px;
  margin: 50px auto;
}
header {
  position: relative;
}

header .logo {
  font-weight: 600;
  font-size: 20px;
  
}

header .presentation {
  margin: 50px 0;
  background: url('./img/16438713231395496.jpg') no-repeat;
  width: 100%;
  height: 500px;
  background-size: cover;
  background-position: center center;
  background-blend-mode: multiply;
  background-color: #bcbcbc;
  position: relative;
}

header .presentation::after {
  content: 'Мистецтво часу, втілене в кожній секунді';
  position: absolute;
  top: 100px;
  left: 50px;
  width: 300px;
  font-size: 40px;
  font-weight: 600;
  color: #fff;
}

header .presentation::before {
  content: 'Якість часу за найкращою ціною';
  position: absolute;
  top: 310px;
  left: 50px;
  width: 300px;
  font-size: 15px;
  font-weight: 300;
  color: #fff;
}

header ul.nav {
  float: right;
  list-style: none;
}

header ul.nav li {
  display: inline;
  margin-left: 25px;
  cursor: pointer;
  transition: opacity 500ms ease;
}

header ul.nav li:hover{
  opacity: 0.5;
}

.shop-cart-button {
  width: 17px;
  height: 18px;
  float: right;
  cursor: pointer;
  transition: color,transform 500ms ease;
}
.shop-cart-button:hover,
.shop-cart-button.active {
  color: #dc3d3d;
  transform: scale(1.5);
}

header .shop-card {
  position: absolute;
  top: 30px;
  right: 0;
  width: 560px;
  z-index: 1;
  background: #fafafa;
  -webkit-box-shadow: 5px 3px 15px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 5px 3px 15px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 5px 3px 15px 0px rgba(0, 0, 0, 0.75);
  padding: 20px;
  padding-bottom: 0;
}

header .shop-card .empty h2 {
  font-size: 20px;
  margin-bottom: 20px;
}

header .shop-card .item{
  width: 100%;
  float: left;
  margin-bottom: 20px;
}

header .shop-card .item img{
  width: 70px;
  float: left;
  margin-right: 20px;
  border-radius: 5px;

}

header .shop-card .item h2{
  font-size: 20px;
}

header .shop-card .item p{
color: #797977;
font-weight: 600;
width: 90%;
}

header .shop-card .item .cross{
  color: #ca5252;
  float: right;
  position: relative;
  top: -25px;
  cursor: pointer;
  transition: color,transform 500ms ease;
}

header .shop-card .item .cross:hover{
color: #dc3d3d;
transform: scale(1.5);
}

header .shop-card .sum {
  position: relative;
  float: left;
  width: 100%;
  font-weight: 600;
  font-size: 20px;
}

header .shop-card button {
  position: relative;
  color: #ca5252;
  border: 1px solid #ca5252;
  width: 40%;
  float: right;
  height: 30px;
  bottom: 27px;
  right: 0px;
  font-weight: 300;
  font-size: 20px;
  border-radius: 10px;
  background-color: #fff;
  cursor: pointer;
  transition: transform 500ms ease;
}

header .shop-card button:hover {
  transform: scale(1.1);
  background-color: #ca5252;
  color: #fff;
}

.categories div:hover {
  border-color:  silver;
  transform: scale(1.1);
}

.categories div {
  display: inline-block;
  background: #f2f2f2;
  border-radius: 50px;
  padding: 10px 20px;
  margin-bottom: 25px;
  margin-right: 15px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 500ms ease;
}

.full-Item {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 100;
  overflow: auto;
}
.full-Item > div {
 width: 600px;
 position: relative;
 margin: 10% auto;
 padding: 40px 30px;
 background: #fff;
 border-radius: 9px;
}

.full-Item img {
  width: 100%;
  border-radius: 10px 10px 0 0;
  transition: transform 300ms ease;
  cursor: pointer;
}

.full-Item  img:hover {
  transform: scale(1.05);
}
.full-Item .add-to-cart {
  position: absolute;
  right: 20px;
  bottom: 20px;
  background: #ca5252;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  color: #fff;
  cursor: pointer;
  border-radius: 50px;
  font-weight:600;
  transition: transform 300ms ease;
}

.full-Item .add-to-cart:hover{
  transform: scale(1.5) translateY(-5px);
}

.show-order {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 100;
  overflow: auto;
}

.order-wrapper {
  width: 600px;
  position: relative;
  margin: 30px auto;
  padding: 40px 30px;
  background: #fff;
  border-radius: 9px;
 }

 .show-order .empty h2 {
  font-size: 20px;
  margin-bottom: 20px;
}

 .show-order .item{
  width: 100%;
  float: left;
  margin-bottom: 20px;
}

 .show-order .item img{
  width: 70px;
  float: left;
  margin-right: 20px;
  border-radius: 5px;

}

.show-order .item h2{
  font-size: 20px;
  margin-bottom: 10px;
}

 .show-order .item p{
color: #797977;
font-weight: 600;
}

.show-order .item .cross{
  color: #ca5252;
  float: right;
  position: relative;
  top: -25px;
  cursor: pointer;
  transition: color,transform 500ms ease;
}

 .show-order .item .cross:hover{
color: #dc3d3d;
transform: scale(1.5);
}

input::-webkit-input-placeholder       {opacity: 1; transition: opacity 0.3s ease;}
input::-moz-placeholder                {opacity: 1; transition: opacity 0.3s ease;}
input:-moz-placeholder                 {opacity: 1; transition: opacity 0.3s ease;}
input:-ms-input-placeholder            {opacity: 1; transition: opacity 0.3s ease;}
input:focus::-webkit-input-placeholder {opacity: 0; transition: opacity 0.3s ease;}
input:focus::-moz-placeholder          {opacity: 0; transition: opacity 0.3s ease;}
input:focus:-moz-placeholder           {opacity: 0; transition: opacity 0.3s ease;}
input:focus:-ms-input-placeholder      {opacity: 0; transition: opacity 0.3s ease;}

.show-order input {
  outline: 0;
  border: 1px solid #797977;
  border-radius: 35px;
  -webkit-border-radius: 35px;
  width: 150px;
  height: 50px;
  line-height: 70px;
  margin: 0 auto 20px 20px;
  padding: 0 20px;
  background: #fff;
  text-align: center;
  font-size: 14px;
}

.show-order .order_input {
  text-align: center;
  margin: 0 20px;
}

.show-order button {
  color: #ca5252;
  border: 1px solid #ca5252;
  width: 40%;
  height: 30px;
  font-weight: 300;
  font-size: 20px;
  border-radius: 10px;
  background-color: #fff;
  cursor: pointer;
  transition: transform 500ms ease;
}

.show-order .order_btn {
  display: flex;
  justify-content: space-around;
}

.show-order button:hover {
  transform: scale(1.1);
  background-color: #ca5252;
  color: #fff;
}

main {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

main .item {
  width: 24%;
  margin-bottom: 50px;
  background: #f5f5f5;
  overflow: hidden;
  position: relative;
  padding-bottom: 20px;
}

main .item img {
  width: 100%;
  border-radius: 10px 10px 0 0;
  transition: transform 300ms ease;
  cursor: pointer;
}

main .item img:hover {
  transform: scale(1.05);
}

main h2, .full-Item h2,
.full-Item p,
main b, .full-Item b {
  margin:  10px 20px;
  color: #333;
}

main p {
  height: 45px;
  margin:  10px 20px;
  color: #333;
}

main h2 {
  margin:  10px 20px;
  color: #333;
  height: 30px;
  font-size: 20px;
  
}

main .add-to-cart {
  position: absolute;
  right: 20px;
  bottom: 13px;
  background: #ca5252;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  color: #fff;
  cursor: pointer;
  border-radius: 50px;
  font-weight:600;
  transition: transform 300ms ease;
}

main .add-to-cart:hover {
  transform: scale(1.5) translateY(-5px);
}

main b,
.full-Item b {
  color: #5fa35f ;
}



footer {
  text-align: center;
  margin-top: 50px;
}

/* media screen */

@media screen and (max-width: 1280px) {
  .conteiner {
    width: 100%;
    padding: 0 10px;
  }
  main .item p {
    width: 80%;
  }
}

@media screen and (max-width: 920px) {
  main .item {
    width: 30%;
  }
}

@media screen and (max-width: 745px) {
  main h2 {
    font-size: 19px;
    margin-bottom: 10px;
  }
  main p {
    font-size: 15px;
    margin-top: 0px;
  }
  main .add-to-cart {
    bottom: 15px;
    width: 30px;
    height: 30px;
    line-height: 30px;
}
.full-Item > div {
  width: 400px;
  margin: 30% auto;
}
}


@media screen and (max-width: 590px) {
  header .presentation::after {
    font-size: 30px;
}

  header .presentation::before {
    top: 220px;
}


  main {
    display: flex;
    width: 95%;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 auto;
}
main .item {
  width: 49%;
  }
  .full-Item > div {
    width: 300px;
  }
  header .shop-card {
    width: 400px;
}
header .shop-card .sum {
  font-size: 18px;
}

header .shop-card button {
  position: relative;
  color: #ca5252;
  border: 1px solid #ca5252;
  width: 38%;
  float: right;
  height: 30px;
  bottom: 27px;
  right: 0px;
  font-weight: 300;
  font-size: 18px;
  border-radius: 10px;
  background-color: #fff;
  cursor: pointer;
  transition: transform 500ms ease;
}
}
@media screen and (max-width: 450px) {
  main h2 {
    font-size: 16px;
    height: 15px;
}
main p {
  font-size: 13px;
  }
}

@media screen and (max-width: 400px) {
  main .item {
    width: 100%;
  }
  main h2 {
    font-size: 23px;
    height: 30px;
}
  main p {
  font-size: 16px;
  }
  main {
    width: 90%;
    justify-content: center;
}
header ul.nav {
  display: none;
}
.shop-cart-button {
  margin-right: 10px;
}

}

/* media show screen */

@media screen and (max-width: 440px) {
  header .shop-card {
    width: 320px;
}
header .shop-card .item h2 {
  font-size: 16px;
}
header .shop-card .item p {
  font-size: 14px;
}
header .shop-card button {
  width: 40%;
  font-size: 16px;
}
header .shop-card .sum {
  font-size: 16px;
}
header .shop-card .item img {
  width: 60px;
}
}

@media screen and (max-width: 680px) {
  .order-wrapper {
    width: 80%;
}
.show-order input {
  width: 140px;
}
}

@media screen and (max-width: 380px) {
  .show-order .item h2 {
    font-size: 16px;
}
.show-order .item p {
font-size: 14px;
}
.show-order .item img {
  width: 60px;
}
}